html,
body
{
  font-family: 'Muli', sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 300;
  height: 100%;
}

h1,
h2,
h3,
h4{
  font-family: 'Montserrat', sans-serif;
  font-weight: bold!important;;
  font-style: normal;
}

h1{
  font-size: 72px!important;
}

h2{
  font-size: 55px;
}

h3{
  font-size: 35px;
}

h4{
  font-size: 5px;
}

h5{
  font-family: 'Muli', sans-serif;
  font-size: 16px;
}

label{
  font-family: 'Muli', sans-serif;
  font-size: 25px;
}

.photo{
  width: 350px;
  height: 350px;
  font-family: 'Muli', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
}

input{
  border: 1px solid #C3BFC9;
  &:active{
    border-color: #000000;
  }
  &:invalid{
    border-color: #FF0001;
  }
  &:disabled{
    border-color: #C3BFC9;
  }
}

